// Import Highway
import Highway from '@dogstudio/highway';

// Import GASP
import Tween from 'gsap';


// Fade
class Fade extends Highway.Transition {
    in( { from, to, done } ) {
        // Remove Old Content
        from.remove();

        // Animation
        Tween.fromTo( to, 0.5, { opacity: 0 }, { opacity: 1, onComplete: done } )
    }

    out( { from, done } ) {
        // Animation
        done();
    }
}

// Export
export default Fade;